import React from 'react';
import { Link } from 'react-router-dom';
import { Input, DatePicker, Button, Modal } from 'antd';
import { CardComponent, CommonPage, SearchBarComponent, StatisticsComponent} from "../components";
const { RangePicker } = DatePicker;
class IndividualCourseList extends React.Component {
    render(){
        const columns = [
            {
                title: "报名时间",
                dataIndex: "create_time",
                key: "create_time"
            },{
                title: "报名ID",
                dataIndex: "id",
                key: "id"
            },{
                title: "学员姓名",
                dataIndex: "name",
                key: "name"
            },{
                title: "出生日期",
                dataIndex: "birthday",
                key: "birthday"
            },{
                title: "手机号码",
                dataIndex: "phone",
                key: "phone"
            },{
                title: "报名费(元)",
                dataIndex: "fee",
                key: "fee"
            },{
                title: "所属教练",
                dataIndex: "coach",
                key: "coach"
            },{
                title: "备注",
                dataIndex: "remark",
                key: "remark"
            }
        ];
        return (
            <CommonPage title="单教" table={{dataSource:[], columns:columns}}>
                <SearchBarComponent
                    left={[{
                        label: "时间",
                        Element: RangePicker,
                        props: {

                        }
                    },{
                        label: "搜索",
                        Element: Input,
                        props: {
                            placeholder: "请输入关键字"
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary">新增</Button>,
                        props: {
                            to: '/course/individual/list/add'
                        }
                    }]}/>
                <CardComponent title="单教数据统计">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="学员人数-总"/>
                        <StatisticsComponent.Item label="学员人数-男"/>
                        <StatisticsComponent.Item label="学员人数-女"/>
                        <StatisticsComponent.Item label="本月新增学员数"/>
                        <StatisticsComponent.Item label="今日新增学员数"/>
                        <StatisticsComponent.Item label="报名费用总额(元)"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default IndividualCourseList;


