import React from 'react';
import API from '../config/api';
import Fetch from '../helpers/fetch';
class Loader extends React.Component{
    constructor(props){
        super(props);
        let compURL = props.compURL || "component-not-found.js";
        this.state = {
            mod: require("../" + compURL).default
        };
    }
    render(){
        let Mod = this.state.mod;
        return Mod?<Mod loadData={(api, postData) => this.loadData(api, postData)}/>: null;
    }
    loadData(api, postData){
        Fetch.post(API[api], postData).then(res => {
            console.log(res)
        });
    }
}
export default Loader;


