let passwordChecker = {
    pattern,
    repeat
};
function pattern(pwd){
    return true;
}

function repeat(pwd, repeatPwd, isFull){
    if(isFull) return pwd === repeatPwd;
    let reg = new RegExp("^" + repeatPwd);
    return reg.test(pwd);
}
export {
    passwordChecker as default
}


