import React from 'react';
import { DatePicker, Input,Button } from 'antd';
import { CommonPage, SearchBarComponent} from "../components";
const { RangePicker } = DatePicker;
class CheckManageList extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "考勤时间",
            dataIndex: "check_time",
            key: "check_time"
        },{
            title: "课程ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "上课时间",
            dataIndex: "class_time",
            key: "class_time"
        },{
            title: "上课地点",
            dataIndex: "location",
            key: "location"
        },{
            title: "带班主教",
            dataIndex: "monitor",
            key: "monitor"
        },{
            title: "助教",
            dataIndex: "assistant",
            key: "assistant"
        },{
            title: "上课状态",
            dataIndex: "status",
            key: "status"
        },{
            title: "学员总人数",
            dataIndex: "student_total",
            key: "student_total"
        },{
            title: "实际出勤人数",
            dataIndex: "student_checked",
            key: "student_checked"
        },{
            title: "操作",
            dataIndex: "operate",
            key: "operate"
        }];
        return (
            <CommonPage title="教练管理" table={{dataSource, columns, total: 20}}>
                <SearchBarComponent
                    left={[{
                        label: "级别",
                        Element: RangePicker,
                        props: {
                            style: {
                                width: 200
                            }
                        }
                    },{
                        label: "搜索",
                        Element: Input,
                        props: {
                            placeholder: "请输入关键字"
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    }]}
                />
            </CommonPage>
        )
    }
}

export default CheckManageList;


