import React from 'react';
import { Input, Button, DatePicker, Select } from 'antd';
import { CommonPage, SearchBarComponent, CardComponent, StatisticsComponent } from '../components/index';
const { RangePicker } = DatePicker;
class SpecialCourseSignUpList extends React.Component{
    render(){
        const { loading } = this.props;
        const dataSource = [];

        const columns = [{
            title: '学员ID',
            dataIndex: 'id',
            key: 'id',
        }, {
            title: '学员姓名',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '学员性别',
            dataIndex: 'gender',
            key: 'gender',
        }, {
            title: '联系电话',
            dataIndex: 'phone',
            key: 'phone',
        }, {
            title: '出勤情况',
            dataIndex: 'status',
            key: 'status',
        }, {
            title: '历史请假次数',
            dataIndex: 'count',
            key: 'count',
        }
        ];
        return (
            <CommonPage title="考勤管理/考勤详情" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "搜索",
                        Element: Input,
                        props: {}
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    }]}/>
                <CardComponent title="数据统计">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="学员人数-总" detail="37,346"/>
                        <StatisticsComponent.Item label="出勤人数" detail="30,000"/>
                        <StatisticsComponent.Item label="请假人数"/>
                        <StatisticsComponent.Item label="旷课人数"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default SpecialCourseSignUpList;


