import React, { Component } from 'react';
import { Table, Button } from 'antd';
import './index.css';
class CommonPageComponent extends Component{
    render(){
        const { className='', title, table=false, pageFooter, children } = this.props;
        let renderTable = null;
        let renderFooter = null;
        if(table){
            const { dataSource, columns, total, rowKey, onChange } = table;

            const pageSize = 15;
            let pageOption = {
                showQuickJumper: true,
                total: total,
                pageSize,
                onChange:(page, pageSize) => {
                    if(onChange){
                        onChange(page, pageSize);
                    }
                }
            };
            renderTable = (<Table className="q-table q-print" dataSource={dataSource} columns={columns} rowKey={rowKey}  pagination={total < pageSize ? false : pageOption}/>);
        }
        if(pageFooter){
            let { ok, cancel } = pageFooter;
            renderFooter = (<footer className="q-page-footer"><Button onClick={cancel.onClick}>{cancel.value}</Button><Button type="primary" onClick={ok.onClick}>{ok.value}</Button></footer>)
        }

        return (<section className={className}>
            <h1 className="q-page-title">{title}</h1>
            <div className="q-page-content">
                {children}
                {renderTable}
            </div>
            {renderFooter}
        </section>)
    }
}
export default CommonPageComponent;


