import React from 'react';
import { Link } from 'react-router-dom';
import { Input, Button } from 'antd';
import { CommonPage, SearchBarComponent } from '../components';
class SpecialCourseList extends React.Component{
    render(){
        const dataSource = [{
            key: 111,
            name: "篮球特训",
            time: "2017-07-29",
            number: 32,
            monitor: "李福招",
            create_time: "2017-07-29 22:00",
            status: '可用',
            link: "www.baidu.com",
            operation: "111"
        }];

        const columns = [{
            title: '课程ID',
            dataIndex: 'key',
            key: 'key',
        }, {
            title: '课程名称',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '课程时间',
            dataIndex: 'time',
            key: 'time',
        }, {
            title: '课程节数',
            dataIndex: 'number',
            key: 'number',
        }, {
            title: '带班主教',
            dataIndex: 'monitor',
            key: 'monitor',
        }, {
            title: '发布时间',
            dataIndex: 'create_time',
            key: 'create_time',
        }, {
            title: '状态',
            dataIndex: 'status',
            key: 'status',
        }, {
            title: '报名链接',
            dataIndex: 'link',
            key: 'link',
        }, {
            title: '操作',
            dataIndex: 'operation',
            key: 'operation',
        }];
        return (
            <CommonPage title='特训班' table={{dataSource, columns, total: 5, royKey: 'id'}}>
                <SearchBarComponent
                    left={[{
                        label: "搜索",
                        Element: Input
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary">新增</Button>,
                        props: {
                            to: "/course/special/add"
                        }
                    }]}/>
            </CommonPage>
        )
    }
}
export default SpecialCourseList;



