import React from 'react';
import { Avatar, Button, DatePicker, Modal, Select } from 'antd';
import { InputComponent } from '../components';
class IndividualCourseAdd extends React.Component{
    render(){
        return (
            <Modal visible={true} title="新增单教" width={700}>
                <form action="javascript:;" className="q-pwd-modify" style={{margin: '0 auto'}}>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">学员姓名：</label>
                        <div className="q-form-detail">
                            <InputComponent placeholder="请输入学员姓名"/>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">学员性别：</label>
                        <div className="q-form-detail">
                            <Select>
                                <Option value="1">男</Option>
                                <Option value="2">女</Option>
                            </Select>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">出生日期：</label>
                        <div className="q-form-detail">
                            <DatePicker style={{width: '100%'}}/>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">手机号码：</label>
                        <div className="q-form-detail">
                            <InputComponent
                                placeholder="请输入手机号码"
                            />
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">报名费用：</label>
                        <div className="q-form-detail">
                            <InputComponent
                                placeholder="请输入报名费用"
                            />
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">所属教练：</label>
                        <div className="q-form-detail">
                            <Select placeholder="请选择教练">
                                <Option value="1">男</Option>
                                <Option value="2">女</Option>
                            </Select>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label">备注：</label>
                        <div className="q-form-detail">
                            <InputComponent
                                placeholder="请输入备注"
                                help="不多于15字"
                            />
                        </div>
                    </div>
                </form>
            </Modal>
        )
    }
}

export default IndividualCourseAdd;


