import React from 'react';
import { Input,Button } from 'antd';
import { CardComponent, CommonPage, SearchBarComponent, StatisticsComponent} from "../components";
class StudentManageList extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "学员ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "姓名",
            dataIndex: "name",
            key: "name"
        },{
            title: "性别",
            dataIndex: "gender",
            key: "gender"
        },{
            title: "出生日期",
            dataIndex: "birthday",
            key: "birthday"
        },{
            title: "手机号码",
            dataIndex: "phone",
            key: "phone"
        },{
            title: "状态",
            dataIndex: "status",
            key: "status"
        },{
            title: "操作",
            dataIndex: "operate",
            key: "operate"
        }];
        return (
            <CommonPage title="学员管理" table={{dataSource, columns, total: 20}}>
                <SearchBarComponent
                    left={[{
                        label: "搜索",
                        Element: Input,
                        props: {
                            placeholder: "请输入关键字"
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    }]}
                />
                <CardComponent title="数据统计">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="学员人数-总" detail="300"/>
                        <StatisticsComponent.Item label="学员人数-男"/>
                        <StatisticsComponent.Item label="学员人数-女"/>
                        <StatisticsComponent.Item label="本月新增学员数"/>
                        <StatisticsComponent.Item label="今日新增学员数"/>
                        <StatisticsComponent.Item label="报名费用总额(元)"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default StudentManageList;


