import React from 'react';
import { Link } from 'react-router-dom';
import { Select, Input,Button } from 'antd';
import { CommonPage, SearchBarComponent} from "../components";
const { Option } = Select;
class CoachManageList extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "教练ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "头像",
            dataIndex: "avatar",
            key: "avatar"
        },{
            title: "姓名",
            dataIndex: "name",
            key: "name"
        },{
            title: "性别",
            dataIndex: "gender",
            key: "gender"
        },{
            title: "手机号码",
            dataIndex: "phone",
            key: "phone"
        },{
            title: "级别",
            dataIndex: "grade",
            key: "grade"
        },{
            title: "注册时间",
            dataIndex: "create_time",
            key: "create_time"
        },{
            title: "状态",
            dataIndex: "status",
            key: "status"
        },{
            title: "操作",
            dataIndex: "operate",
            key: "operate"
        }];
        return (
            <CommonPage title="教练管理" table={{dataSource, columns, total: 20}}>
                <SearchBarComponent
                    left={[{
                        label: "级别",
                        Element: Select,
                        props: {
                            style: {
                                width: 200
                            },
                            placeholder: "请选择教练级别"
                        },
                        children: [<Option key="111">666</Option>]
                    },{
                        label: "搜索",
                        Element: Input,
                        props: {
                            placeholder: "请输入关键字"
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary">新增</Button>,
                        props: {
                            to: '/manage/coach/add'
                        }
                    }]}
                />
            </CommonPage>
        )
    }
}
export default CoachManageList;


