import React from 'react';
import { Button } from 'antd';
import { Link } from 'react-router-dom';
import { CardComponent, CommonPage, InputComponent } from '../components/index';
import { passwordChecker } from '../utils';
class UserAccount extends  React.Component{
    constructor(props){
        super(props);
        this.state = {
            oldPwd: {
                value: '',
                hide: true,
                errMsg: ''
            },
            newPwd: {
                value: '',
                hide: true,
                errMsg: ''
            },
            repeatPwd: {
                value: '',
                hide: true,
                errMsg: ''
            }
        };
    }
    render(){
        return (
           <CommonPage title="我的账号">
                <CardComponent title="账号信息">
                    <div className="q-pwd-modify">
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label">手机号码：</label>
                            <div className="q-form-detail">
                                <span>这里应该有一个手机号码</span>
                            </div>
                        </div>
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label ">角色：</label>
                            <div className="q-form-detail">
                                <span>培训运营</span>
                            </div>
                        </div>
                    </div>
                </CardComponent>
                <CardComponent title="修改密码">
                    <form className="q-pwd-modify">
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label">当前密码：</label>
                            <div className="q-form-detail">
                                <InputComponent
                                    type="password"
                                    placeholder="请输入当前账号密码"
                                    help={<span>如忘记密码，请<Link to="/forget">重置密码</Link></span>}
                                />
                            </div>
                        </div>
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label">新密码：</label>
                            <div className="q-form-detail">
                                <InputComponent
                                    type="password"
                                    placeholder="请输入新密码"
                                    help={<span>密码长度不少于6位</span>}
                                />
                            </div>
                        </div>
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label">确认密码：</label>
                            <div className="q-form-detail">
                                <InputComponent
                                    type="password"
                                    placeholder="请再次输入新密码"
                                />
                            </div>
                        </div>
                        <div className="q-form-group q-btn">
                            <Button type="primary" tabIndex="4">确认</Button>
                        </div>
                    </form>
                </CardComponent>
           </CommonPage>
        )
    }
}
export default UserAccount;



