import React from 'react';
import { Input, Button } from 'antd';
import { CardComponent, CommonPage, SearchBarComponent, StatisticsComponent} from "../components/";
class RegularCourseSignUpList extends React.Component{
    render(){
        const { loading } = this.props;
        const dataSource = [];

        const columns = [{
            title: '课程ID',
            dataIndex: 'id',
            key: 'id',
        }, {
            title: '课程名称',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '上课时间',
            dataIndex: 'class_time',
            key: 'class_time',
        }, {
            title: '上课地点',
            dataIndex: 'location',
            key: 'location',
        }, {
            title: '报名人数(总/男/女)',
            dataIndex: 'sign_stat',
            key: 'sign_stat',
        }, {
            title: '报名费(总/男/女)',
            dataIndex: 'fee_stat',
            key: 'fee_stat',
        },{
            title: '操作',
            dataIndex: 'operate',
            key: 'operate',
        }
        ];
        return (
            <CommonPage title="平时班" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "搜索",
                        Element: Input,
                        props: {}
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    }]}/>
                <CardComponent title="数据统计">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="报名人数-总" detail="37,346"/>
                        <StatisticsComponent.Item label="报名人数-男" detail="30,000"/>
                        <StatisticsComponent.Item label="报名人数-女"/>
                        <StatisticsComponent.Item label="本月新增报名数"/>
                        <StatisticsComponent.Item label="今日新增报名数"/>
                        <StatisticsComponent.Item label="报名费用总额(元)"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default RegularCourseSignUpList;


