import React from 'react';
import { Avatar, Select, Button, Modal } from 'antd';
import { CommonPage, InputComponent, AvatarSettingComponent } from '../../components';
import './add-edit.css';
const { Option } = Select;
class CoachTemplate extends React.Component{
    render(){
        const { subTitle, modalOption } = this.state;
        return (
            <CommonPage title='教练管理'>
                <section className="q-section">
                    <h3 className="q-section-title">{subTitle}</h3>
                    <div className="q-section-body">
                        <form action="javascript:;">
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label">头像：</label>
                                <div className="q-form-detail">
                                    <Button>选择图片</Button>
                                    <Avatar size="small"/>
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label">姓名：</label>
                                <div className="q-form-detail">
                                    <InputComponent
                                        placeholder="请输入教练姓名"
                                    />
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label">性别：</label>
                                <div className="q-form-detail">
                                    <Select>
                                        <Option value="1">男</Option>
                                        <Option value="2">女</Option>
                                    </Select>
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label">手机号码：</label>
                                <div className="q-form-detail">
                                    <InputComponent
                                        placeholder="请输入手机号码"
                                    />
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label">级别：</label>
                                <div className="q-form-detail">
                                    <Select>
                                        <Option value="1">主教</Option>
                                        <Option value="2">助教</Option>
                                    </Select>
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label">个人简介：</label>
                                <div className="q-form-detail">
                                    <InputComponent
                                        placeholder="请输入个人相关简介"
                                        help="不多于15个汉字"
                                        maxLength="15"/>
                                </div>
                            </div>
                        </form>
                    </div>
                </section>
                <AvatarSettingComponent visible={true}/>
            </CommonPage>
        )
    }
}

export default CoachTemplate;


