
import React from 'react';
import { Checkbox, TimePicker, Select, Cascader   } from 'antd';
import { CommonPage, StepsComponent, TabsComponent, PhoneComponent,InputComponent, FormComponent,MapComponent,RichTextComponent } from '../../components';
import { weeks } from "../../config/CONSTANT";
import addressOptions from '../../config/cascader-address-options';
import "../../../css/supplement.css";
const CheckBoxGroup = Checkbox.Group;
const Option = Select.Option;
class RegularCourseAddEditTemplate extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            activeKey:'2',
            inputElement: null,
            text: 'hello 111'
        };
    }
    render(){
        const footerConfig = {
            ok: {
                value: "下一步",
                onClick: () => this.setState({
                    activeKey:'2'
                })
            },
            cancel: {
                value: '取消',
                onClick: () => this.setState({
                    activeKey:'1'
                })
            }
        };
        return (
            <CommonPage table={false} className="q-sub-page" title="新增平时课程" pageFooter={footerConfig}>
                <StepsComponent current={1}>
                    <StepsComponent.Item>1.基础信息</StepsComponent.Item>
                    <StepsComponent.Item>2.课程详情</StepsComponent.Item>
                </StepsComponent>
                <div className="q-pane">
                    <PhoneComponent className="q-pane-phone"/>
                    <div className="q-pane-rest q-mrg-l15">
                        <TabsComponent activeKey={this.state.activeKey}>
                            <TabsComponent.TabPane tab="基础信息" key="1">
                                <section className="q-sub-section">
                                    <h3 className="q-sub-section-title">基础信息</h3>
                                    <div className="q-sub-section-main">
                                        <FormComponent direction="vertical" >
                                            <FormComponent.Group  label="课程名称" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent
                                                    placeholder="请输入课程名称"
                                                    maxLength="15"
                                                    error="请输入课程名称"
                                                    help="课程名称长度不能长于15个字"
                                                />
                                            </FormComponent.Group>
                                            <FormComponent.Group label="上课时间" labelWidth="120px" marginBottom='0' style={{width: "330px"}}>
                                                <div className="q-multiline">
                                                    <CheckBoxGroup size="small">
                                                        {weeks.map((item) => (
                                                            <Checkbox size="small" value={item.value} key={item.value}>{item.label}</Checkbox>
                                                        ))}
                                                    </CheckBoxGroup>
                                                    <div className="q-timepicker-group">
                                                        <TimePicker/>
                                                        <span className="q-short-dash">-</span>
                                                        <TimePicker/>
                                                    </div>
                                                    <div className="q-timepicker-add">
                                                        <a className="link">添加时间段</a>
                                                        <a className="link">删除时间段</a>
                                                    </div>
                                                </div>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="上课区域" labelWidth="120px" style={{width: "330px"}}>
                                                <Cascader options={addressOptions}/>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="上课区域" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent placeholder="请输入上课球馆的详细地址" wrapRef={(r) => this.getInputRef(r)}/>
                                            </FormComponent.Group>
                                        </FormComponent>
                                        <div className="q-map-container">
                                            <MapComponent getLatLng={(latLng) => console.log(latLng)} lat="23.17072" lng="113.34234" bigAddress="广东省广州市天河区" inputElement={this.state.inputElement}/>
                                        </div>
                                    </div>
                                </section>
                                <section className="q-sub-section">
                                    <h3 className="q-sub-section-title">其他信息</h3>
                                    <div className="q-sub-section-main">
                                        <FormComponent direction="vertical" >
                                            <FormComponent.Group  label="报名费用" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent
                                                    placeholder="请输入报名费用"
                                                    help="小数点后保留2位，如：20.00。单位：元。不填默认为免费"
                                                />
                                            </FormComponent.Group>
                                            <FormComponent.Group label="课程节数" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent placeholder="请输入课程节数"/>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="咨询电话" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent placeholder="请输入联系电话"/>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="带班主教" labelWidth="120px" style={{width: "330px"}}>
                                                <Select placeholder="请选择带班主教">
                                                    <Option value="李福招">李福招</Option>
                                                </Select>
                                            </FormComponent.Group>
                                        </FormComponent>
                                    </div>
                                </section>
                            </TabsComponent.TabPane>
                            <TabsComponent.TabPane tab="课程详情" key="2">
                                <section className="q-sub-section">
                                    <h3 className="q-sub-section-title">课程详情</h3>
                                    <div className="q-sub-section-main">
                                        <RichTextComponent maxLength={500} text={this.state.text} getText={() => {}}/>
                                    </div>
                                </section>
                            </TabsComponent.TabPane>
                        </TabsComponent>
                    </div>
                </div>
            </CommonPage>
        )
    }
    componentDidMount(){

    }
    getInputRef(ref){
        if(ref && !this.state.inputElement){
            this.setState({
                inputElement: ref
            });
        }
    }
}
export default RegularCourseAddEditTemplate;




