import React from 'react';
import { Input } from 'antd';
import './input.css'
export default class InputComponent extends React.Component{
    render(){
        let { error, help, wrapRef, ...others } = this.props;
        let hasError = typeof error === 'object' ? true : typeof error === 'string' && error.length ;
        let hasHelp = typeof help === 'object' ? true : typeof help === 'string' && help.length ;
        return (
            <span className={hasError? 'q-invalid q-input': 'q-valid q-input'}>
                <input type="text" className="ant-input" ref={wrapRef} {...others}/>
                <div className="q-msg-block">
                    {hasHelp ? (<span className="q-help-msg">{help}</span>) : null}
                    {hasError ? (<span className="q-error-msg">{error}</span>) : null}
                </div>
            </span>
        )
    }
}




