import React from 'react';
import { Input, Button, DatePicker } from 'antd';
import { CommonPage, SearchBarComponent} from '../components/index';
class SpecialCourseSignUpList extends React.Component{
    render(){
        const { loading } = this.props;
        const dataSource = [];

        const columns = [{
            title: '报名时间',
            dataIndex: 'create_time',
            key: 'create_time',
        }, {
            title: '课程ID',
            dataIndex: 'id',
            key: 'id',
        }, {
            title: '课程名称',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '上课时间',
            dataIndex: 'time',
            key: 'time',
        }, {
            title: '报名费用(元)',
            dataIndex: 'fee',
            key: 'fee',
        }, {
            title: '总课时',
            dataIndex: 'total',
            key: 'total',
        }, {
            title: '剩余课时',
            dataIndex: 'left',
            key: 'left',
        }, {
            title: '考勤记录(出勤/请假/旷课)',
            dataIndex: 'record',
            key: 'record',
        },{
            title: '状态',
            dataIndex: 'status',
            key: 'status',
        }
        ];
        return (
            <CommonPage title="学员管理/岑洁平" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "搜索",
                        Element: Input,
                        props: {}
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    }]}/>
            </CommonPage>
        )
    }
}

export default SpecialCourseSignUpList;


