import React from 'react';
import { Link } from 'react-router-dom';
import { Input, Button } from 'antd';
import { CommonPage, SearchBarComponent } from "../components";
const columns = [{
    title: '课程ID',
    dataIndex: 'id',
    key: 'id',
}, {
    title: '课程名称',
    dataIndex: 'name',
    key: 'name',
}, {
    title: '课程时间',
    dataIndex: 'class_time',
    key: 'class_time',
}, {
    title: '课程节数',
    dataIndex: 'class_num',
    key: 'class_num',
}, {
    title: '带班主教',
    dataIndex: 'monitor',
    key: 'monitor',
}, {
    title: '发布时间',
    dataIndex: 'create_time',
    key: 'create_time',
}, {
    title: '状态',
    dataIndex: 'status',
    key: 'status',
}, {
    title: '报名链接',
    dataIndex: 'link',
    key: 'link',
}, {
    title: '操作',
    dataIndex: 'operate',
    key: 'operate',
}
];
class RegularCourseList extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            dataSource: props.loadedData || [],
            total: props.total || [],
            params: {
                key: '',
                page: 1,
                row: 15
            },
            loading: false
        }
    }
    render(){
        const state = this.state;
        let table = {
            dataSource: state.dataSource,
            columns: columns,
            total: 20,
            // total: state.total,
            loading: state.loading,
            rowKey: (record) => record.id,
            onChange: (page, pageSize) => this.updateTable({page})
        };
        return (
            <CommonPage title="平时班" table={table}>
                <SearchBarComponent
                    left={[{
                        label: "搜索",
                        Element: Input,
                        props: {
                            onChange: (e) => this.controlInput(e.target.value),
                            onPressEnter: (e) => this.updateTable({key:e.target.value}),
                            value: state.params.key,
                            placeholder: '请输入关键字'
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary">新增</Button>,
                        props: {
                            to: "/course/regular/add"
                        }
                    }]}/>
            </CommonPage>
        )
    }
    controlInput(value){
        this.setState(prevState => {
            return {
                params: {
                    ...prevState.params,
                    key: value
                }
            }
        });
    }
    updateTable(updateBlock){
        this.props.loadData('COURSENORMALLIST', {
            ...this.state.params,
            ...updateBlock
        });
    }
}
export default RegularCourseList;


