import React from 'react';
import { DatePicker, Input,Button } from 'antd';
import { CommonPage, SearchBarComponent } from "../components";
const { RangePicker } = DatePicker;
class SpecialSignDetail extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "报名ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "报名时间",
            dataIndex: "create_time",
            key: "create_time"
        },{
            title: "微信头像",
            dataIndex: "avatar",
            key: "avatar"
        },{
            title: "微信昵称",
            dataIndex: "nickname",
            key: "nickname"
        },{
            title: "学员姓名",
            dataIndex: "name",
            key: "name"
        },{
            title: "学员性别",
            dataIndex: "gender",
            key: "gender"
        },{
            title: "学员生日",
            dataIndex: "birthday",
            key: "birthday"
        },{
            title: "手机号码",
            dataIndex: "phone",
            key: "phone"
        },{
            title: "报名费(元)",
            dataIndex: "fee",
            key: 'fee'
        }];
        return(
            <CommonPage title="平时班报名/报名明细" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "时间",
                        Element: RangePicker,
                        props: {
                            style: {
                                width: 200
                            }
                        }
                    },{
                        label: "搜索",
                        Element: Input,
                        props: {
                            placeholder: "请输入关键字"
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: "打印"
                    },{
                        Element: Button,
                        value: "导出CSV"
                    }]}
                />
            </CommonPage>
        )
    }
}

export default SpecialSignDetail;


